.screen {
  background-color: "#bbd0c9";
  height: 100vh;
}
.box {
  margin-top: 2%;
}
.nav-header {
  position: relative;
  animation-name: nav-header;
  animation-duration: 1s;
}

@keyframes nav-header {
  0% {
    top: -40px;
  }
  100% {
    left: 0px;
    top: 0px;
  }
}
.form {
  padding: 3%;
  border: black solid 2px;
  position: relative;
  animation-name: form;
  animation-duration: 1s;
}
.label {
  padding-bottom: 5%;
}
@keyframes form {
  0% {
    left: -50%;
  }
  100% {
    left: 0px;
  }
}
/*
* {
  box-sizing: border-box;
}
*/
body {
  font-family: Verdana, sans-serif;
}

.mySlides {
  display: none;
}
.img {
  vertical-align: middle;
}

/* Slideshow container */
.slideshow-container {
  max-width: 1000px;
  position: relative;
  margin: auto;
}

/* Caption text */
.text {
  color: #f2f2f2;
  font-size: 15px;
  padding: 8px 12px;
  position: absolute;
  bottom: 8px;
  width: 100%;
  text-align: center;
}

/* Number text (1/3 etc) */
.numbertext {
  color: #f2f2f2;
  font-size: 12px;
  padding: 8px 12px;
  position: absolute;
  top: 0;
}

/* The dots/bullets/indicators */
.dot {
  height: 15px;
  width: 15px;
  margin: 0 2px;
  background-color: #bbb;
  border-radius: 50%;
  display: inline-block;
  transition: background-color 0.6s ease;
}

.active {
  background-color: #717171;
}

/* Fading animation */
.fade {
  animation-name: fade;
  animation-duration: 1.5s;
}

@keyframes fade {
  from {
    opacity: 0.4;
  }
  to {
    opacity: 1;
  }
}

/* On smaller screens, decrease text size */
@media only screen and (max-width: 300px) {
  .text {
    font-size: 11px;
  }
}
